<template>
<v-dialog lazy max-width='600' v-model="$store.state.showPlanetsVisibilityDialog">
  <v-card v-if="$store.state.showPlanetsVisibilityDialog" transparent class="secondary white--text">
    <v-card-title><div class="headline">Planets Visibility</div></v-card-title>
    <v-card-text>Night from {{ startDate.format('MMMM Do') }} to {{ endDate.format('MMMM Do') }}</v-card-text>
    <v-card-text>
      <div>
        <v-layout row wrap>
          <v-flex xs1 offset-xs2><span>Rise</span></v-flex>
          <v-flex xs1><span>Set</span></v-flex>
          <v-flex xs8>
            <v-layout row justify-space-between>
              <span>12:00</span><span>18:00</span><span>00:00</span><span>06:00</span><span>12:00</span>
            </v-layout>
          </v-flex>
        <template v-for="obj in objs">
          <v-flex xs2>{{obj.name}}</v-flex>
          <v-flex xs1>{{formatTime(obj.rise)}}</v-flex>
          <v-flex xs1>{{formatTime(obj.set)}}</v-flex>
          <v-flex xs8>
            <div :style='sunBackgroundStr'>&nbsp;
              <div v-html="planetBackgroundStr(obj)"></div>
            </div>
          </v-flex>
        </template>
        </v-layout>
      </div>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer><v-btn class="blue--text darken-1" flat @click.native="$store.state.showPlanetsVisibilityDialog = false">Close</v-btn>
    </v-card-actions>
  </v-card>
</v-dialog>
</template>

<script>

import Moment from 'moment'

export default {
  data: function () {
    return {
      objs: [
        this.$stel.getObj('Sun'),
        this.$stel.getObj('Moon'),
        this.$stel.getObj('Mercury'),
        this.$stel.getObj('Venus'),
        this.$stel.getObj('Mars'),
        this.$stel.getObj('Jupiter'),
        this.$stel.getObj('Saturn')
      ]
    }
  },
  methods: {
    formatTime: function (jdm) {
      let d = new Date()
      d.setMJD(jdm)
      let utc = Moment.utc(d)
      utc.local()
      return utc.format('HH:mm')
    },
    planetBackgroundStr: function (obj) {
      let d = new Date()
      d.setMJD(obj.rise)
      let rise = Moment.utc(d)
      rise.local()
      d.setMJD(obj.set)
      let set = Moment.utc(d)
      set.local()

      var hourToPercent = function (h) {
        return (h >= 12) ? Math.round((h - 12) / 24 * 100) : Math.round((h + 12) / 24 * 100)
      }

      let riseP = hourToPercent(rise.hours())
      let setP = hourToPercent(set.hours())
      if (setP > riseP) {
        return "<div style='z-index: 100; position: absolute; background-color: rgb(200, 200, 50); left: " + riseP + '%; min-width: ' + (setP - riseP) + "%; top: 7px; height: 8px;'></div>"
      } else {
        let ret = "<div style='z-index: 100; position: absolute; background-color: rgb(200, 200, 50); left: 0%; min-width: " + setP + "%; top: 7px; height: 8px;'></div>"
        ret += "<div style='z-index: 100; position: absolute; background-color: rgb(200, 200, 50); right: 0%; min-width: " + (100 - riseP) + "%; top: 7px; height: 8px;'></div>"
        return ret
      }
    }
  },
  computed: {
    sunBackgroundStr: function () {
      var sun = this.$stel.getObj('Sun')
      let brightness = []
      let d = new Moment(this.startDate)

      let obs = this.$stel.core.observer.clone()
      for (let i = 0; i < 25; i++) {
        obs.utc = d
        d.local()
        sun.update(obs)
        let alt = sun.alt
        brightness.push(alt / (Math.PI / 2))
        d.add(1, 'hours')
      }
      obs.destroy()

      var txt = 'position: relative; background: linear-gradient(to right, '
      for (let i = 0; i < 25; i++) {
        let bi = (brightness[i] + 0.1) * 5
        bi = bi > 1 ? 1 : bi
        bi = bi < 0 ? 0 : bi
        txt += 'rgb(' + Math.round(53 * bi) + ', ' + Math.round(173 * bi) + ', ' + Math.round(211 * bi) + ') ' + Math.round(i / 24 * 100) + '% '
        if (i !== 24) {
          txt += ','
        }
      }
      txt += '); min-width: 100%;'
      return txt
    },
    startDate: function () {
      var sun = this.$stel.getObj('Sun')
      let u = this.$store.state.stel.observer.utc
      if (u < sun.rise) {
        // It's still night (in the morning), display last night's planets visibility
        u = u - 1
      }
      let d = new Date()
      d.setMJD(u)
      d = new Moment(d)
      d.local()
      d.hours(12)
      d.minutes(0)
      d.seconds(0)
      return d
    },
    endDate: function () {
      let d = new Moment(this.startDate)
      d.add(1, 'd')
      return d
    }
  }
}
</script>

<style>
.input-group {
  margin: 0px;
}
</style>



